@media screen and (max-width:599px){
    .input-calendar{
      display:flex;
      width: 1%;
      flex-direction: column;
    }
  }
.option-report-daily{
    width: 82%;
    padding: 10px 15px;
    margin: 8px 0;
    margin-left: 4%;
    box-sizing: border-box;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    outline: none;
    background-color: honeydew;
  }
  .option-report-daily:focus{
    border: 3px solid #555;
  }
  .option-report-monthly{
    width: 82%;
    padding: 10px 15px;
    margin: 8px 0;
    margin-left: 4%;
    box-sizing: border-box;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    outline: none;
    background-color: honeydew;
  }
  .option-report-monthly:focus{
    border: 3px solid #555;
  }
  .pensioners-form-container{
    background-color:whitesmoke;
  }