.li {
  float: left;
  font-size: 14px;
  color: white;
  margin-left: 25px;
  display: flex;
}

.li a {
  display: block;
  color: white;
  text-align: center;
  justify-content: center;
  text-decoration: none;
}

.li a:hover {
  background-color: #c01217;
}

.client-footer {
  background-image: linear-gradient(to bottom, #c01217, #96090e);
  height: auto;
  width: 100%;
  font-size: 14px;
  color: white;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  justify-content: center;
  text-align: center;
  padding: 15px;
  z-index: 25;
  position: fixed;
  bottom: 0;
  left: 0;
}

.active {
  font-size: 14px;
  margin-left: 20px;
}

.dropdown-icon {
  size: 5px;
}

.userImg {
  height: 3%;
  width: 23px;
}

.container1 {
  float: right;
  justify-content: flex-end;
}

.list {
  width: 100%;
}

.user {
  /* float:right; */
  color: white;
  font-size: 14px;
}

.NewLoan {
  background-color: #c01217;
}

.settingsIcn {
  width: 23px;
  height: 3%;
  margin-left: 20px;
  margin-right: 30px;
  color: white;
  float: right;
}

.text {
  margin-left: 5px;
  color: white;
  font-size: 14px;
}

.dropdown-item {
  background-color: #c01217;
  color: white;
  font-size: 14px;
  height: auto;
  padding: 20px;
}

.menuChild {
  font-size: 14px;
  padding: 20px;
}

@media only screen and (max-width: 360px) {
  .client-footer {
    background-image: linear-gradient(to bottom, #c01217, #96090e);
    height: auto;
    width: 100%;
    font-size: 14px;
    color: white;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    justify-content: center;
    text-align: center;
    padding: 15px;
    z-index: 25;
    position: fixed;
    bottom: 0;
    left: 0;
  }
  .footer-label{
    display:flex;
  }
}
