.calendar {
  margin-top: 5%;
  width: 50%;
}
.option {
  margin-top: 2%;
}
.input-receivable{
  width:98.5%;
}
.card-client-businessub {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  width: 40%;
  height: auto;
  position: absolute;
  top: 43%;
  margin-bottom: 40%;
  transform: translate(90%, -40%);
}
.card-client-businessub:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}
.business-logo {
  width: 30%;
  height: 30%;
  margin-left: 35%;
  margin-top: 10%;
}
.option-report-daily{
  width: 82%;
  padding: 10px 15px;
  margin: 8px 0;
  margin-left: 4%;
  box-sizing: border-box;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  outline: none;
  background-color: honeydew;
}
.option-report-daily:focus{
  border: 3px solid #555;
}
.option-report-monthly{
  width: 82%;
  padding: 10px 15px;
  margin: 8px 0;
  margin-left: 4%;
  box-sizing: border-box;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  outline: none;
  background-color: honeydew;
}
.option-report-monthly:focus{
  border: 3px solid #555;
}
.sblform-container{
  background-color:whitesmoke;
}
.label{
  display:flex;
  color:rgb(87, 86, 86);
}
@media only screen and (max-width: 360px){
  .card-client-businessub {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    width: 70%;
    height: auto;
    position: absolute;
    top: 30%;
    margin-top: 25%;
    transform: translate(25%, -40%);
  }

  .business-logo {
    width: 30%;
    height: 30%;
    margin-left: 35%;
    margin-top: 10%;
  }
}
@media only screen and (max-width:360px){
  .input-calendar{
    display:flex;
    width: 1%;
    flex-direction: column;
  }
}