@media (max-width: 768px) {
  .nav {
    flex-direction: column;
    align-items: center;
  }

  .nav .nav-wrap {
    flex-direction: column;
    align-items: center;
  }
}
.footer {
  background-image: linear-gradient(to bottom, #c01217, #96090e);
  height: auto;
  width: 100%;
  font-size: 14px;
  color: white;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  justify-content: center;
  text-align: center;
  padding: 15px;
  left: 0;
}

.nav {
  border-top: 2px solid #cc3333;
  background-image: linear-gradient(to bottom, #c01217, #96090e);
  border-bottom: 2px solid #cc3333;
  width:100%;
  height:auto;
  font-size: 14px;
  color: white;
  overflow:hidden;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: .5%;
  position:fixed;
  z-index:25;
  top:0;
}
.nav-left {
  display: flex;
  margin-top: 8px;
  margin-left: 10px;
}
.nav-center {
  display: inline;
  justify-content: left;
}
.nav-right {
  display: flex;
  margin-top: 8px;
  margin-right: 20px;
}
.nav-wrap {
  flex: 1;
  display: flex;
  justify-content: space-evenly;
  max-width: 60%;
}
.nav-wrap .nav-center {
  display: inline;
}

.home-icon {
  font-size: large;
}

.drop-btn {
  background-color: #c01217;
  color: white;
  padding: 10px;
  font-size: 14px;
  border: none;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  color: black;
  background-color: #f1f1f1;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.dropdown-content div {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown-content div:hover {
  background-color: #ddd;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown:hover .dropbtn {
  background-color: #cc3333;
  /* border-bottom: 4px solid red; */
}

.settings-icon {
  font-size: medium;
  margin-left: 10px;
}

.card-admin {
