.card-report {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  width: 35%;
  height: auto;
  margin-top: 10%;
  margin-left: 30%;
}
.card-report:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}
.option{
  width:210px;
  display: flex;
}
.button1{
  border-radius: 4px;
  background-color: #1e61f0;
  border: none;
  color: #ffffff;
  text-align: center;
  font-size: 14px;
  padding: 7px;
  width: 100px;
  transition: all 0.5s;
  cursor: pointer;
  margin-bottom: 20px;
}
.button1:hover {
  background-color: #84a7f3;
}
.button2{
  border-radius: 4px;
  background-color: #1e61f0;
  border: none;
  color: #ffffff;
  text-align: center;
  font-size: 14px;
  padding: 7px;
  width: 100px;
  transition: all 0.5s;
  cursor: pointer;
  margin-bottom: 20px;
}
.button2:hover {
  background-color: #84a7f3;
}
.table-container{
  display:flex;
  width:100%;
  flex-direction: column;
}
.table{
  width:50%;
}
tbody{
  margin-right: 20%;
}
.label{
  font-weight: 500;
}
@media(max-width: 900px){
  .option{
    width:100px;
  }
  .button1 .button2{
    display: flex;
  }
  .button2 {
    margin-left: 15%;
  }
  .button1 {
    margin-left: 15%;
  }
}
@media(min-width: 3788px){
  .table{
    margin-left: 40%;
  }
  .button2{
    margin-left: 37%;
  }
}
.notice{
  font-size: 14px;
  margin-left: 15%;
}
.table-container-incentive{
  display:flex;
  width:100%;
  flex-direction: column;
  margin-top: 20%;
  margin-bottom: 20%;
}
.btn-container{
  justify-content: center;
}