.card-client {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  width: 50%;
  height: auto;
  position: absolute;
  top: 30%;
  margin-top: 14%;
  transform: translate(65%, -60%);
}
.card-client:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}
.ml-logo {
  width: 30%;
  height: 30%;
  margin-left: 35%;
  margin-top: 10%;
}
.option-newloan-client {
  width: 82%;
  padding: 10px 15px;
  margin: 8px 0;
  margin-left: 4%;
  box-sizing: border-box;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  outline: none;
  background-color: honeydew;
}
.option-newloan-client:focus {
  border: 3px solid #555;
}
.input {
