.card-newLoan {
  width: 30%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: 35%;
  height: auto;
  box-shadow: 0 15px 16px 0 rgba(0, 0, 0, 0.2);
  position: absolute;
  top: 50%;
  left: 15%;
  transform: translate(-50%, -50%);
  background-color: white;
}
.table-bl {
  width: 70%;
  text-align: left;
  margin-left: 15%;
  margin-top: 10%;
  position: absolute;
  top: 30%;
  left: 35%;
  opacity: 0.95;
  transform: translate(-50%, -50%);
}
.table-bl th {
  border-collapse: collapse;
  padding: 2%;
  text-align: center;
}
.table-bl td {
  text-align: center;
  padding: 2%;
}
.table-bl tr:nth-child(even) {
  background-color: #fff;
  border: solid grey 1px;
}
table tr:nth-child(odd) {
  background-color: #dff0d8;
  border: solid grey 1px;
}
.al-text {
  color: grey;
  display: flex;
  justify-content: center;
}

.dropdown-item {
  background-color: #c01217;
  color: white;
  font-size: 14px;
  height: auto;
  padding: 20px;
}
