.btn-containers {
  display: flex;
  flex-flow: row wrap;
  align-items: stretch;
  justify-content: center;
}
.button-item {
  /* border: 2px solid black; */
  border-radius: 4px;
  background-color: #1e61f0;
  border: none;
  color: #ffffff;
  text-align: center;
  font-size: 14px;
  padding: 7px;
  width: 100px;
  transition: all 0.5s;
  cursor: pointer;
  margin: 5px;
  margin-bottom: 20px;
  display: flex;
}

.button-item span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}

.button-item span:after {
  content: "\00bb";
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}

.button-item:hover span {
  padding-right: 25px;
}

.button-item:hover span:after {
  opacity: 1;
  right: 0;
}
.button-item:hover {
  background-color: #84a7f3;
}
@media only screen and (max-width: 360px) {
  .card-client {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    width: 70%;
    height: auto;
    position: absolute;
    top: 30%;
    margin-top: 25%;
    transform: translate(24%, -40%);
  }

  .ml-logo {
    width: 30%;
    height: 30%;
    margin-left: 35%;
    margin-top: 10%;
  }
}
